:root {
  --bg: #000000;
  --clr-1: #8B0000;
  --clr-2: #8B0000;


  --blur: 1rem;
  --fs: clamp(3rem, 8vw, 7rem);
  --ls: clamp(-1.75px, -0.25vw, -3.5px);
}

body {
  min-height: 100vh;
  display: grid;
  place-items: center;
  background-color: #000000;
  background-color: var(--bg);
  color: #fff;
  font-family: "Inter", "DM Sans", Arial, sans-serif;
}

*,
*::before,
*::after {
  font-family: inherit;
  box-sizing: border-box;
}

.content {
  text-align: center;
}

.title {
  font-size: clamp(3rem, 8vw, 7rem);
  font-size: var(--fs);
  font-weight: 500;
  letter-spacing: clamp(-1.75px, -0.25vw, -3.5px);
  letter-spacing: var(--ls);
  position: relative;
  overflow: hidden;
  background: #000000;
  background: var(--bg);
  margin: 0;
}

.subtitle {
  font-size: clamp(1.5rem, 5vw, 2rem);
  font-weight: 400;
  letter-spacing: clamp(-1.75px, -0.25vw, -3.5px);
  letter-spacing: var(--ls);
  position: relative;
  overflow: hidden;
  background: #000000;
  background: var(--bg);
  margin: 0;
}

.aurora {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  mix-blend-mode: darken;
  pointer-events: none;
}

.aurora__item {
  overflow: hidden;
  position: absolute;
  width: 50vw;
  height: 50vw;
  background-color: #8B0000;
  background-color: var(--clr-1);
  border-radius: 42% 36% 34% 32% / 31% 28% 44% 40%;
  -webkit-filter: blur(1rem);
          filter: blur(1rem);
  -webkit-filter: blur(var(--blur));
          filter: blur(var(--blur));
  mix-blend-mode: overlay;
}

.aurora__item:nth-of-type(1) {
  top: -50%;
  -webkit-animation: aurora-border 10s ease-in-out infinite,
    aurora-1 15s ease-in-out infinite alternate;
          animation: aurora-border 10s ease-in-out infinite,
    aurora-1 15s ease-in-out infinite alternate;
}

.aurora__item:nth-of-type(2) {
  background-color: #8B0000;
  background-color: var(--clr-2);
  right: 0;
  top: 0;
  -webkit-animation: aurora-border 10s ease-in-out infinite,
    aurora-2 15s ease-in-out infinite alternate;
          animation: aurora-border 10s ease-in-out infinite,
    aurora-2 15s ease-in-out infinite alternate;
}

.aurora__item:nth-of-type(3) {
  background-color: #8B0000;
  background-color: var(--clr-1);
  left: 0;
  bottom: 0;
  -webkit-animation: aurora-border 10s ease-in-out infinite,
    aurora-3 12s ease-in-out infinite alternate;
          animation: aurora-border 10s ease-in-out infinite,
    aurora-3 12s ease-in-out infinite alternate;
}

.aurora__item:nth-of-type(4) {
  background-color: #8B0000;
  background-color: var(--clr-2);
  right: 0;
  bottom: -50%;
  -webkit-animation: aurora-border 10s ease-in-out infinite,
    aurora-4 26s ease-in-out infinite alternate;
          animation: aurora-border 10s ease-in-out infinite,
    aurora-4 26s ease-in-out infinite alternate;
}

@-webkit-keyframes aurora-1 {
  0% {
    top: 0;
    right: 0;
  }

  50% {
    top: 100%;
    right: 75%;
  }

  75% {
    top: 100%;
    right: 25%;
  }

  100% {
    top: 0;
    right: 0;
  }
}

@keyframes aurora-1 {
  0% {
    top: 0;
    right: 0;
  }

  50% {
    top: 100%;
    right: 75%;
  }

  75% {
    top: 100%;
    right: 25%;
  }

  100% {
    top: 0;
    right: 0;
  }
}

@-webkit-keyframes aurora-2 {
  0% {
    top: -50%;
    left: 0%;
  }

  60% {
    top: 100%;
    left: 75%;
  }

  85% {
    top: 100%;
    left: 25%;
  }

  100% {
    top: -50%;
    left: 0%;
  }
}

@keyframes aurora-2 {
  0% {
    top: -50%;
    left: 0%;
  }

  60% {
    top: 100%;
    left: 75%;
  }

  85% {
    top: 100%;
    left: 25%;
  }

  100% {
    top: -50%;
    left: 0%;
  }
}

@-webkit-keyframes aurora-3 {
  0% {
    bottom: 0;
    left: 0;
  }

  40% {
    bottom: 100%;
    left: 75%;
  }

  65% {
    bottom: 40%;
    left: 50%;
  }

  100% {
    bottom: 0;
    left: 0;
  }
}

@keyframes aurora-3 {
  0% {
    bottom: 0;
    left: 0;
  }

  40% {
    bottom: 100%;
    left: 75%;
  }

  65% {
    bottom: 40%;
    left: 50%;
  }

  100% {
    bottom: 0;
    left: 0;
  }
}

@-webkit-keyframes aurora-4 {
  0% {
    bottom: -50%;
    right: 0;
  }

  50% {
    bottom: 0%;
    right: 40%;
  }

  90% {
    bottom: 50%;
    right: 25%;
  }

  100% {
    bottom: -50%;
    right: 0;
  }
}

@keyframes aurora-4 {
  0% {
    bottom: -50%;
    right: 0;
  }

  50% {
    bottom: 0%;
    right: 40%;
  }

  90% {
    bottom: 50%;
    right: 25%;
  }

  100% {
    bottom: -50%;
    right: 0;
  }
}

@-webkit-keyframes aurora-border {
  0% {
    border-radius: 37% 29% 27% 27% / 28% 25% 41% 37%;
  }

  25% {
    border-radius: 47% 29% 39% 49% / 61% 19% 66% 26%;
  }

  50% {
    border-radius: 57% 23% 47% 72% / 63% 17% 66% 33%;
  }

  75% {
    border-radius: 28% 49% 29% 100% / 93% 20% 64% 25%;
  }

  100% {
    border-radius: 37% 29% 27% 27% / 28% 25% 41% 37%;
  }
}

@keyframes aurora-border {
  0% {
    border-radius: 37% 29% 27% 27% / 28% 25% 41% 37%;
  }

  25% {
    border-radius: 47% 29% 39% 49% / 61% 19% 66% 26%;
  }

