  }

  100% {
    border-radius: 37% 29% 27% 27% / 28% 25% 41% 37%;
  }
}
.myCarousel {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 20px;
    background: transparent;
    color: white;
    text-align: left;
}

.carousel .control-prev.control-arrow:before,
.carousel .control-next.control-arrow:before {
    content: '';
    border: none;
    background: none;
}

.carousel .control-prev.control-arrow:after {
    content: '<';
    font-size: 2em;
    color: white;
    transition: -webkit-transform 0.1s;
    transition: transform 0.1s;
    transition: transform 0.1s, -webkit-transform 0.1s;
}

.carousel .control-next.control-arrow:after {
    content: '>';
    font-size: 2em;
    color: white;
    transition: -webkit-transform 0.1s;
    transition: transform 0.1s;
    transition: transform 0.1s, -webkit-transform 0.1s;
}

.carousel .slide img {
    max-height: 567px;
    width: auto;
}

@-webkit-keyframes moveRight {
    0% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }

    50% {
        -webkit-transform: translateX(10px);
                transform: translateX(10px);
    }

    100% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}

@keyframes moveRight {
    0% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }

    50% {
        -webkit-transform: translateX(10px);
                transform: translateX(10px);
    }

    100% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}

@-webkit-keyframes moveLeft {
    0% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }

    50% {
        -webkit-transform: translateX(-10px);
                transform: translateX(-10px);
    }

    100% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}

@keyframes moveLeft {
    0% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }

    50% {
        -webkit-transform: translateX(-10px);
                transform: translateX(-10px);
    }

    100% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}

.carousel .control-next.control-arrow:before {
    -webkit-animation: moveRight 1s infinite;
            animation: moveRight 1s infinite;
}

.carousel .control-prev.control-arrow:before {
    -webkit-animation: moveLeft 1s infinite;
            animation: moveLeft 1s infinite;
}

.carousel .control-prev.control-arrow:hover:before,
.carousel .control-next.control-arrow:hover:before {
    -webkit-transform: scale(1);
            transform: scale(1);
}

.carousel .control-prev.control-arrow:hover:before,
.carousel .control-next.control-arrow:hover:before {
    -webkit-transform-origin: center center;
            transform-origin: center center;
}
.fade-container {
