/* Estilos para la barra de desplazamiento en Chrome, Safari, Brave, Edge (versión nueva), Opera y Opera GX */
::-webkit-scrollbar {
    width: 12px;
}

::-webkit-scrollbar-track {
    background: #000;
}

::-webkit-scrollbar-thumb {
    background-color: #000;
    border-radius: 20px;
    border: 3px solid #000;
}
